<script>
import { defineComponent, ref } from "vue";
import { useIsMobile, useIsTablet, useIsSmallDesktop } from '../utils/composables';
import { NSpace, NButton, NGrid, NGi, NCard, NText, NNumberAnimation, NInputNumber, NLayoutFooter, NCountdown } from 'naive-ui';

import { storeConnect } from '../store/connect.js';

export default defineComponent({
  components: {
    NSpace, NButton, NGrid, NGi, NCard, NText, NNumberAnimation, NInputNumber, NLayoutFooter, NCountdown
},
setup () {
    //const countdownActiveRef = ref(false);
    const countdownActiveRef = ref(true); //Wenn Countdown laufen soll
    const deadlineMintClaim = 'November 10 2022 19:39:59 UTC';//19:59:59
    //const deadlineMintClaim = 'November 8 2022 04:59:59 UTC';
    const totalDeadlineMintClaim = Date.parse(deadlineMintClaim) - Date.parse(new Date());
    // common
    const isMobileRef = useIsMobile();
    const isTabletRef = useIsTablet();
    const isSmallDesktopRef = useIsSmallDesktop();

    return {
      isMobile: isMobileRef,
      isTablet: isTabletRef,
      isSmallDesktop: isSmallDesktopRef,
      countdownActive: countdownActiveRef,
      storeConnect,
      totalDeadlineMintClaim
    }
  }
})
</script>
